<template>
  <div id="catalog">
    <h2 v-if="headerText">{{headerText}}</h2>
    <div :id="'categ-'+categ.id" v-for="categ in getItems" :key="categ.id">
      <h2>{{categ.text | uppercase}}</h2>
      <div class="product-container">
        <app-product :key="item.id" v-for="item in categ.items" :item="item"/>
      </div>
    </div>
    <h2 v-if="footerText">{{footerText}}</h2>
    <transition name="delivery">
      <div id="delivery-form-back" v-show="showDelivery" @click="hideDeliveryEvent">
        <div id="delivery-form">
          <app-delivery-form/>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
import Product from "./Product";
import DeliveryForm from "./DeliveryForm";
import { mapGetters, mapMutations } from "vuex";
export default {
  components: {
    "app-product": Product,
    "app-delivery-form": DeliveryForm
  },
  methods: {
    ...mapMutations(["hideDelivery"]),
    hideDeliveryEvent(event) {
      if (event.target.id == "delivery-form-back") this.hideDelivery();
    }
  },
  computed: {
    ...mapGetters(["getItems", "showDelivery"]),
    headerText() {
      switch (this.$route.path) {
        case "/pizza-nagatinskaya":
          return "Пицца на Нагатинской. Доставка за 30 минут";
        case "/pizza-anokhina":
          return "Пицца на ул. Академика Анохина. Доставка за 30 минут";
      }
    },
    footerText() {
      switch (this.$route.path) {
        case "/pizza-nagatinskaya":
          return "Доставка еды и пиццы на Нагатинской. ";
        case "/pizza-anokhina":
          return "Доставка еды и пиццы на Академика Анохина";
      }
    }
  },
  created() {
    console.log(this.$route.path);
    switch (this.$route.path) {
      case "/pizza-nagatinskaya":
        this.$store.commit("setFirstRoute", this.$route.path);
        break;
      case "/pizza-anokhina":
        this.$store.commit("setFirstRoute", this.$route.path);
        break;
    }
  }
};
</script>
<style scoped>
#catalog {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-around;
  width: 100%;
}
h2 {
  padding: 20px;
  padding-top: 50px;
  text-align: center;
  font-size: 2em;
}
.product-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
}
#delivery-form-back {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: #00000055;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 10;
}
#delivery-form {
  display: block;
  flex: 1;
  max-width: 800px;
  padding: 20px;
  background-color: white;
  border: 1px solid #ddd;
}
.delivery-enter-active,
.delivery-leave-active {
  transition: opacity 0.1s;
}
.delivery-enter,
.delivery-leave-to {
  opacity: 0;
}
</style>



